import React from "react";
import {ContainerLink} from "../components/form-container/container-link/container-link";
import {Button} from "@ya.praktikum/react-developer-burger-ui-components";

export const typeLinksFooter = {
  alreadyRegistered: <ContainerLink textDescription=" ?" textLink="" to='/login' key='alreadyRegistered'/>,
  rememberPassword: <ContainerLink textDescription=" ?" textLink="" to='/login' key='rememberPassword'/>,
  forgotPassword: <ContainerLink textDescription=" ?" textLink=" ?" to='/forgot-password' key='forgotPassword'/>,
  newUser:<ContainerLink textDescription="  ?" textLink="" to='/register' key='newUser'/>,
}


type Tbutton = {
  label:string;
  key:string;
}

export function createButton({label, key}:Tbutton):JSX.Element {
  return (<Button htmlType="submit" type="primary" size="medium" children={label} key={key}/>)
}

export const profileButtons = [
  <Button htmlType="reset" type="secondary" size="medium" children="" key='cancel'/>,
  <Button htmlType="submit" type="primary" size="medium" children="" key='save'/>
]


