import React from "react";
import {
  COLOR_PRIMARY,
  DIGITS_SMALL,
  TColorsText,
  TFunctionComponentWithChildren,
  TSizesDigits
} from "../../../utils/types";

export type TPropsDigitsElement = {
  size?: TSizesDigits;
  extraClass?: string;
  color?: TColorsText;
} & TFunctionComponentWithChildren;

/**
 *     
 * @param size -  ,    figma
 * - __DIGITS_SMALL__ = "text_type_digits-default" -  
 * - DIGITS_MEDIUM = "text_type_digits-medium"
 * - DIGITS_LARGE = "text_type_digits-large"
 * @param color -  
 * - ERROR
 * - __PRIMARY__ -  
 * - ACCENT
 * - SUCCESS
 * - INACTIVE
 * @param extraClass -  
 * @param children
 */
export function Digits({
                         size = DIGITS_SMALL,
                         extraClass = '',
                         color = COLOR_PRIMARY,
                         children
                       }: TPropsDigitsElement): JSX.Element {
  return (
    <p className={`text ${size} ${color} ${extraClass} `}>{children}</p>
  )
}